html {
  height: 100vh;
  width: 100vw;
}

body {
  margin: 0;
}

.redBackground {
  background-color: #e4153e99;
}

.floatingEditor {
  opacity: .93;
  height: 90%;
  max-width: 70vw;
  position: absolute;
  top: 5%;
  right: 10px;
}

.floatingViewer {
  width: 100vw;
  height: 100vh;
  position: absolute;
  left: 0;
  right: 0;
}

table {
  border-collapse: collapse;
  border: 1px solid #000;
}

.collapsible {
  background-color: #eee;
  font-size: 15px;
}

.active, .collapsible:hover {
  background-color: #ccc;
}

.collapsible:after {
  content: "➕";
  color: #fff;
  float: right;
  margin-left: 5px;
  font-size: 12px;
}

.active:after {
  content: "➖";
}

